.form {
  margin: 1rem 0;
  height: auto;
  overflow: scroll;
}

.control {
  margin-bottom: 0.5rem;
}

.control label {
  font-weight: bold;
  margin-bottom: 5px;
  font-size: small;
  display: block;
}

.control input {
  font: inherit;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
}

.actions {
  display: flex;
  justify-content: flex-end;
  gap: 0.5rem;
  margin-top: 1rem;
  margin-top: 30px;
}

.actions button {
  font: inherit;
  color: #5a1a01;
  cursor: pointer;
  background-color: transparent;
  border: none;
  border-radius: 5px;
  padding: 0.2rem 1rem;
}

.actions button:hover,
.actions button:active {
  background-color: #ffe6dc;
  border: 1px solid rgb(220, 216, 216);
}
.actions button {
  border: 1px solid rgb(220, 216, 216);
}
.actions .submit {
  border: 1px solid #5a1a01;
  background-color: #5a1a01;
  color: white;
}

.actions .submit:hover,
.actions .submit:active {
  background-color: #7a2706;
}
.errorMesage{
  font-size: small;
  color: red;
}